@import '../../../../styles/variables.scss';
@import '../../../../styles/fonts/index.scss';

.cabinet-orders-details{
    //.cabinet-orders-details__list-head{}
    &__list-head{
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-bottom: 30px;
        flex-direction: column;
    }
    //.cabinet-orders-details__container-list-title{}
    &__container-list-title{
        display: flex;
        position: relative;
        justify-content: space-between;
        align-items: center;
        @include mobile{
            flex-direction: column;
            align-items: flex-start;
        }

    }
    //.cabinet-orders-details__list-title{}
    &__list-title{
        @include h(4);
        &-mb {
            @include h(4);
            margin-bottom: 30px;
        }
        &--discard{
            margin-top: 15px;
            font-size: 12px;
            color: #c3c3c3;
        }
    }
    //.cabinet-orders-details__list-link{}
    &__list-link{
        margin-top: 20px;
        color: $b;
    }
    //.cabinet-orders-details__link--blue{}
    &__link--blue{
        padding: 0;
    }

}
