@import '../../../../styles/variables.scss';
@import '../../../../styles/fonts/index.scss';

.cabinet-orders-details{
     //.cabinet-orders-details__comment-send{}
     &__comment-send{
        height: 36px;
        background: rgba(243, 239, 243, 0.3);
        box-shadow: inset -2px 0px 4px rgba(0, 0, 0, 0.04), inset 2px 0px 4px rgba(0, 0, 0, 0.04);
        width: 100%;
        @include flex(sc);
        &>div{
            // min-width: 265px !important;
        }
    }
    //.cabinet-orders-details__comment-input{}
    &__comment-input{
        padding: 0;
    }
    //.cabinet-orders-details__comment-buttons{}
    &__comment-buttons{
        display: flex;
        &>button{
            background-color: transparent !important;
        }
    }

    &__comment{
        &-status-connection{
            //.cabinet-orders-details__comment-status-connection--active{}
            &--active{
                width: 7px;
                height: 7px;
                position: absolute;
                background-color: #11ff00;
                display: block;
                z-index: 1;
                border-radius: 50%;
                right: 17px;
                bottom: 32px;
            }
            //.cabinet-orders-details__comment-status-connection--disactive{}
            &--disactive{
                width: 7px;
                height: 7px;
                position: absolute;
                background-color: #ff0000;
                display: block;
                z-index: 1;
                border-radius: 50%;
                right: 17px;
                bottom: 32px;
            }
        }
    }
}